@font-face {
  font-family: "icomoon";
  src: url("fonts/icomoon.eot?pki5jk");
  src: url("fonts/icomoon.eot?pki5jk#iefix") format("embedded-opentype"),
    url("fonts/icomoon.ttf?pki5jk") format("truetype"),
    url("fonts/icomoon.woff?pki5jk") format("woff"),
    url("fonts/icomoon.svg?pki5jk#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

